"use client";

import Image from "next/image";
import { useRouter } from 'next/navigation';
import { Box, Container, Paper, Typography, Button, IconButton } from '@mui/material';
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import { styled } from '@mui/material/styles';
import { useState, useRef } from 'react';

const StyledButton = styled(Button)({
  backgroundColor: '#E8E1C4',
  color: '#777777',
  '&:hover': {
    backgroundColor: '#d8d0c0',
  },
  textTransform: 'none',
  boxShadow: 'none',
  padding: '8px 16px',
  borderRadius: '6px',
  fontWeight: 500,
  fontSize: '14px',
  minWidth: 'auto'
});

const ImageContainer = styled(Box)({
  width: 220,
  height: 220,
  borderRadius: '50%',
  overflow: 'hidden',
  position: 'relative',
  backgroundColor: '#F3F4F6',
  border: '4px solid #fff'
});

const CameraButton = styled(IconButton)({
  position: 'absolute',
  bottom: 10,
  right: 10,
  backgroundColor: '#E8E1C4',
  padding: '12px',
  width: 50,
  height: 50,
  zIndex: 2,
  '&:hover': {
    backgroundColor: '#d8d0c0',
  }
});

const EditProfileButton = styled(IconButton)({
  padding: 0,
  '&:hover': {
    backgroundColor: 'transparent',
  },
  width: '55px',
  height: '55px',
});

const CustomContainer = styled(Container)({
  maxWidth: '1200px',
  padding: '0 24px',
  margin: '0 auto'
});

const details = [
  { label: "Email Address:", value: "infomail@exaple.com" },
  { label: "Communication preference", value: "Call, Email" },
  { label: "Phone no.:", value: "123 456 7890" },
  { label: "New Listing Frequency", value: "Daily" },
  { label: "Default Search City:", value: "Pleasant Hill" },
];

export default function ProfilePage() {
  const router = useRouter();
  const fileInputRef = useRef(null);
  const [profileImage, setProfileImage] = useState("/api/placeholder/220/220");

  const handleEditProfile = () => {
    router.push('/edit-profile');
  };

  const handleImageClick = () => {
    fileInputRef.current?.click();
  };

  const handleImageChange = (event) => {
    const file = event.target.files?.[0];
    if (file) {
      const imageUrl = URL.createObjectURL(file);
      setProfileImage(imageUrl);
      
      // Here you would typically upload the image to your server
      // For now, we're just updating the local state
      // const formData = new FormData();
      // formData.append('image', file);
      // await uploadImage(formData);
    }
  };

  return (
    <Box sx={{ minHeight: '100vh', bgcolor: '#F9FAFB', py: 4 }}>
      <CustomContainer>
        <Paper
          elevation={0}
          sx={{
            borderRadius: '12px',
            boxShadow: '0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1)',
            bgcolor: '#fff',
            height: '380px',
            minHeight: '200px'
          }}
        >
          <Box sx={{ p: 4, pt: 6 }}>
            <Box sx={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'flex-start',
              mb: -5,
              ml: 0.9,
              gap: 3
            }}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 4 }}>
                <Box
                  position="relative"
                  sx={{
                    mt:-3,
                    position: 'relative',
                    top: '65px',
                    ml: 2.5,
                  }}
                >
                  <ImageContainer>
                    <Image
                      src={profileImage}
                      alt="Profile"
                      width={220}
                      height={220}
                      style={{ objectFit: 'cover', width: '100%', height: '100%' }}
                      priority
                    />
                  </ImageContainer>
                  <CameraButton size="small" onClick={handleImageClick}>
                    <CameraAltIcon sx={{ fontSize: 18, color: '#374151' }} />
                  </CameraButton>
                  <input
                    type="file"
                    ref={fileInputRef}
                    accept="image/*"
                    style={{ display: 'none' }}
                    onChange={handleImageChange}
                  />
                </Box>
                <Typography
                  variant="h1"
                  sx={{
                    fontSize: '2rem',
                    fontWeight: 600,
                    color: '#484848',
                    lineHeight: 1.2,
                    ml: '32px',
                    mt:-5,
                  }}
                >
                  John Doe
                </Typography>
              </Box>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <EditProfileButton onClick={handleEditProfile}>
                  <Image
                    src="/Images/Edit-Profile-icon.svg"
                    alt="Edit Profile"
                    width={35}
                    height={35}
                  />
                </EditProfileButton>
                <StyledButton>
                  Change Password
                </StyledButton>
              </Box>
            </Box>

            {/* Adjusted the margin-top (mt) to reduce the gap between the name and details */}
            <Box sx={{
              display: 'grid',
              gridTemplateColumns: 'repeat(2, 1fr)',
              gap: 2.5,
              pl: '310px',
              mt: -7,  // Reduced margin-top to move the details closer to the name
              mb: 9
            }}>
              {details.map((detail, index) => (
                <Box
                  key={index}
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: 2
                  }}
                >
                  <Typography
                    sx={{
                      color: '#777777',
                      fontSize: '0.875rem',
                      fontWeight: 500,
                      width: '180px',
                      flexShrink: 0
                    }}
                  >
                    {detail.label}
                  </Typography>
                  <Typography
                    sx={{
                      color: '#777777',
                      fontSize: '0.875rem',
                      fontWeight: 500,
                      opacity: 0.7,
                    }}
                  >
                    {detail.value}
                  </Typography>
                </Box>
              ))}
            </Box>
          </Box>
        </Paper>
      </CustomContainer>
    </Box>
  );
}
